___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 100%;
  max-width: 600px;
}

.todays-forecast {
  gap: 10px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.current-day header {
  margin-bottom: 20px;
}

#location {
  font-size: 24px;
  margin: 0;
}

#date {
  font-size: 18px;
  color: #555;
  margin: 0;
}

.weather-info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px 0;
}

.weather-icon-container {
  flex: 1;
  display: flex;
  justify-content: center;
}

.weather-icon {
  width: 120px;
  height: 120px;
}

.temperature {
  display: flex;
  justify-content: space-around;
  flex: 2;
}

.high-temp,
.low-temp {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.temp-digit {
  font-size: 32px;
  font-weight: bold;
}

.temp-label {
  font-size: 16px;
  color: #555;
  margin-top: 5px;
}

#todaysWeatherDescription {
  margin-top: 20px;
  font-size: 18px;
  color: #333;
}

form {
  margin-bottom: 20px;
}

input {
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
  max-width: 300px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

#loading {
  margin-top: 20px;
}

.current-weather {
  display: grid;
  grid-template-rows: auto auto auto auto;
  gap: 10px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.current-weather header h2 {
  font-size: 20px;
  margin: 0;
}

.weather-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.weather-icon-container {
  text-align: center;
}

.weather-description {
  margin-top: 10px;
  font-size: 14px;
  color: #555;
}

.current-temp {
  text-align: center;
}

.current-temp .temp-digit {
  font-size: 36px;
  font-weight: bold;
}

.weather-stats {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.stat {
  text-align: center;
}

.stat-title {
  font-size: 14px;
  color: #555;
}

.stat-info {
  font-size: 18px;
  font-weight: bold;
}
